.bb_content{
	width:1000px;
	margin: 0 auto;
	background: #fff;
	font-family:Microsoft YaHei;
	font-size:14px;
	ul{
	    list-style: none;
	}
	.bb-tit{
		height:100px;
		background:#f1f2f6;
	    line-height: 100px;
	    img{
	        padding-left: 4px;
			position: relative;
			top: 6px;
	    }
	    .tit-sure{
	    	font-size:22px;
	    	color:#333333;
	    	font-weight:600;
	    }
	    .sure-search{
	    	float: right; 
	    	position: relative;
			    input{
		    	    height: 45px;
				    width: 260px;
				    outline: none;
				    border-radius: 4px 0 0 4px;
				    padding-left: 15px;
				    border: 1px solid #dcdcdc;
				    margin-right: 53px;
		    	}
		    	i{
		    	       font-size: 26px;
				    color: #bcbbbb;
				    border: 1px solid;
				    height: 45px;
				    position: absolute;
				    padding: 4px 0 0px 0px;
				    float: right;
				    text-align: center;
				    right: 4px;
				    border-radius: 0px 4px 4px 0;
				    line-height: 37px;
				    width: 50px;
				    top: 28px;
		    	}
	    	 

	    }
	}
	.config{
		height:210px;
		border-top: 2px solid #f37e01;
		.fz_orange{
			color:#ff4400;
		}
		.hint{
			text-align:center;
			padding-top: 55px;
			padding-bottom: 55px;
			i{
				color:#f37e01;
				font-size:80px;
			}
			.hint-hm{
				font-size: 18px;
				font-weight: 600;
				div.a{
					color:#666666;
					    padding-top: 18px;
				}
			}
		}
		a{
		 text-decoration: none; 
    		color: #66a5d0;
		}
		.order-tab{
			width:100%;
			border:1px solid #c7b5a8;
			text-align:center;
			td{
				border:1px solid #c7b5a8;
				    padding: 16px 0;
			}
			.order-tit{
				color:#8f7d6d;
     			font-weight: 600;
     			width:100px;
			}
			.order-content{
				width:142px;
				color:#666666;
			} 
			.state{
				color:#ff4400;
				font-weight:600;
			}
		}
		.order-info{
				color:#999999;
				padding:20px 0 45px 10px;
			}

		.pay-info{
			margin-top: 50px;
			.info-it {
				>span{
					margin: 0 20px;
				}
				    
			}
			.pay-type{
				width:150px;
				font{
					font-weight:600;
					color:#ff4400;
				}
			}
			.pay-time{
				width:200px;
			}
			.pay-id{
				width:250px;
			}
		}

		.pay{
			width:620px;
			margin:85px auto;
			.pay-psw{
				padding: 0 38px;
				font{
					margin-right:5px;
				}
				.psw-input{
					width:65px;
					height:55px;
					margin: 0 -3px 0 -3px;
				    outline: none;
			        font-size: 24px;
				}
			}
			.pay-back{
				margin-top:45px;
			    background: #f6f6f6;
			    height: 50px;
			    line-height: 50px;
			    border-bottom: 1px solid #e5e5e5;
			    border-top: 1px solid #e5e5e5;
				div>span{
					display:inline-block;
					overflow: hidden;
    				white-space: nowrap;
				}
				.pay-type{
					width:140px;
					font{
						font-weight:600;
						color:#666666;
					}
				}
				.pay-time{
					width:250px;
					font{
						color:#418ec2;
					}
				}
				.pay-id{
					width:220px;
					font{
						font-weight:600;
						color:#666666;
					}
				}
			}
			.pay-cash{
				.cash{
					font-size:18px;
					color:#666666;
				}
				.org{
					color:#ff4404;
					font-size:20px;
				}
				.money{
					font-size:26px;
					font-weight:600;
				}

			}

		}

	}
	.text-align{
		text-align:center;
	}

	.btn{
		width:135px;
		height:40px; 
	    display: inline-block;
	    line-height: 40px;
	    color: #fff;
	    border-radius: 4px; 
	    font-weight: 600;
	    cursor: pointer;
	}
	.btn-org{
		background:#ff4400;
		letter-spacing: 3px;
	}
	.info-org{
		background:#ff4400;
		padding: 0 20px;
		font-weight: 400;
		    width: auto;
		.info-ids{
			font-size:20px;
		}
	}



