@import "index";
@import "store";
@import "shopping";
@import "information";
@import "login";
*{
    font-family: 'Microsoft YaHei';
}

* {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -o-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.header{
    background-color:#f37e01;
    width:100%;
    height:42px;
    .header-center{
        width:1000px;
        height:100%;
        margin:0 auto;
        .center-right{
            width:320px;
            height:100%;
            float:right;
            line-height:42px;
            a{
                font-size:14px;
                height:10px;
                color:#fff;
                text-decoration:none;
                font-size:14px;
                i{
                    font-size:12px;
                    color:#fff;
                    margin-left:2px;
                }
            }
            .line{
                color:#fff;
                display:inline-block;
                width:1px;
                height:14px;
                background-color:#fff;
                position:relative;
                top:2px;
                margin:0 1px;
            }
        }
    }

}